<template>
  <div id='app'> 
       <image-item @on-open-dia="onOpenDia"></image-item>
       <controls></controls>
       <keep-alive>
          <transition name="fade">
            <detail-window v-show="isShow" @on-dia-hide="onDiaHide" :articles="articles"></detail-window>
          </transition>
       </keep-alive>
       
  </div>
</template>

<script>
import ImageItem from './components/ImageItem/ImageItem'
import Controls from './components/Controls/Controls'
import DetailWindow from './components/DetailWindow/DetailWindow'
export default {
  components: {
    ImageItem,
    Controls,
    DetailWindow
  },
  data() {
    return {
      isShow: false,
      articles: []
    }
  },
  methods: {
    onDiaHide() {
      this.isShow = false
    },
    onOpenDia(articles) {
      this.articles = [...articles]
      this.isShow = true
    }
  }
}
</script>

<style lang='less' scoped>
#app {
  height: 100vh;
  width: 100vw;
  position: relative;
}
/* 开始过渡阶段,动画出去阶段 */
.fade-enter-active,.fade-leave-active{
  transition: all 0.5s ease-out;
}
/* 进入开始 */
.fade-enter{
  transform: translateY(-500px);
  opacity: 0;
}
/* 出去终点 */
.fade-leave-active{
  transform: translateY(500px);
  opacity: 0;
}
</style>



