<template>
 <div class="image-item">
   <template v-for="(item,index) in getImages" >
     <figure  :key="item.id" :class="['img-figure',{'is-inverse':item.isInverse}]" :style="{width:'256px',left:item.pos.left+'px',top:item.pos.top+'px',transform:item.isCenter?'':`rotate(${item.rotate
                        ? item.rotate
                        : 0}deg)`,position:'absolute',zIndex:item.isCenter?11:0}"
                @click.stop.prevent="handleClick(item)">
                <img :src="require('../../common/images/'+item.imgURL)" :alt="item.title" />
                <figcaption>
                    <h2 class="img-title" @click.stop.prevent="openDia(item)">{{item.title}}</h2>
                    <div class="img-back" @click.stop.prevent="handleClick(item)">
                        <p >{{item.desc}}</p>
                    </div>
                </figcaption>
            </figure>
   </template>
 </div>
</template>

<script type="text/ecmascript-6">
import { mapActions, mapGetters } from 'vuex'
export default {
  mounted() {
     /* eslint-disable */
    const id = this.getImages.length / 2  - 1
    this.cur_id = id
    this.reRange({id})
    let that = this
    window.onresize = () => {
      console.log(that.cur_id)
      that.reRange({id:that.cur_id})
    }
  },
  props: {
 
  },
  computed: {
    ...mapGetters(['getImages'])
  },
  data() {
    return {
      cur_id:0
    }
  },
  methods: {
     ...mapActions(['reRange','setInverse','setCenter']),
    handleClick(item) {
      //  翻转和居中图片
        let {id} = item
      if (item.isCenter) {
        this.setInverse({id})
      } else {
        this.setCenter({id})
        this.reRange({id})
      }
      this.cur_id = id
    },
    openDia(item){
      this.handleClick(item)
      this.$emit('on-open-dia',item.articles)
    }
  }
}
</script>

<style scoped lang="less">
.image-item {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: #ddd;
  perspective: 2100px;
  .img-figure{
    position: absolute;
    box-sizing: border-box;
    width: 256px;
    height: 300px;
    margin: 0;
    padding: 8px;
    cursor: pointer;
    transition: transform 0.6s ease-in-out, left 0.6s ease-in-out,
    top 0.6s ease-in-out;
    transform-origin: 0 50% 0;
    border-radius: 3px;
    background-color: #eee;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 3px 3px -2px rgba(0, 0, 0, 0.2),
      0 1px 8px 0 rgba(0, 0, 0, 0.12);
    transform-style: preserve-3d;
    &.is-inverse{
      transform: translate(256px) rotateY(180deg);
    }
  }
  .img-back{
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 16px;
    transform: rotateY(180deg) translateZ(1px);
    text-align: center;
    color: #666;
    background: #eee;
    backface-visibility: hidden;
   
  }
   figcaption{
      text-align: center;
      color: #222;
      .img-title {
        font-size: 16px;
        margin: 10px 0 0 0;
      &:hover{
          color:deepskyblue;
        }
      }
    }
} 
</style>



