import * as types from './mutation-types'

export const setInverse = ({
  commit
}, data) => {
  commit(types.SET_INVERSE, data)
}
export const setCenter = ({
  commit
}, data) => {
  commit(types.SET_CENTER, data)
}
export const reRange = ({commit}, data) => {
  commit(types.RE_RANGE, data)
}



