import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const App = () => import('@/App')

export default new Router({
  mode: 'history',
  base: __dirname,
  linkActiveClass: 'active',
  routes: [{
    path: '/',
    redirect: '/app'
  }, {
    name: 'app',
    path: '/app',
    component: App
  }]
})



