import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import * as actions from './actions'
import * as getters from './getters'
import data from '../common/datas/data'
Vue.use(Vuex)
let images = data.map((img, index) => ({
  ...img,
  imgURL: img.fileName,
  id: index,
  isCenter: false,
  isInverse: false,
  pos: {},
  rotate: ''
}))
const state = {
  images
}
export default new Vuex.Store({state, mutations, actions, getters})



