<template>
 <div class="dialog" @click.self="hideDialog">
   <div class="main" :class="{'is-inverse':isInverse}">
    <div class="content">
      <ul v-show="!showContent">
        <li class="article"  v-for="(item,index) in articles" :key="index" @click="detail(item.content)">
          <span class="title">{{item.title}}</span>
          <span class="pub-date">{{item.pubDate}}</span>
          <!-- <span class="read-count">{{item.readCount}}</span> -->
        </li>
      </ul>
      <div class="article-content" v-show="showContent">
        <div class="article-content-inner">
          <div>{{content}}</div>
        </div>
        <div class="back" @click="back"><span>返回</span></div>
      </div>

    </div>
   </div>
 </div>
</template>

<script>
 export default {
   props: {
     articles: {
       type: Array
     }
   },
   data() {
     return {
       isInverse: false,
       showContent: false,
       content: ''
     }
   },
   methods: {
     hideDialog() {
       this.isInverse = false
       this.showContent = false
       this.content = ''
       this.$emit('on-dia-hide')
     },
     detail(content) {
       this.isInverse = true
       this.showContent = true
       this.content = content
     },
     back() {
       this.content = ''
       this.isInverse = false
       this.showContent = false
     }
   },
   components: {

   }
 }
</script>

<style scoped lang="less">

@import "../../common/less/mixins";
.dialog{
  z-index: 997;
  width: 100%;
  height: 100%;
  position: absolute;
  top:0;
  background-color: rgba(0,0,0,0.3);
  .main{ 
        position: relative;
    transition: transform 0.6s ease-in-out, left 0.6s ease-in-out,
    top 0.6s ease-in-out;
    &.is-inverse{
      transform:  rotateY(180deg);
    }
    z-index: 998;
    background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 70%;
    height: 80%;
    margin: 5% auto;
    .bg-img('./bg.png');
    .content{ 
      margin: 0 auto; 
      width: 70%;
      height: 70%; 
      overflow: auto;
      position: absolute;
      top: 15%;
      left: 18%;
      .article{
        height: 40px;
        font-size: 16px;
        line-height:40px;
        color: indianred;
        cursor: pointer;
        .title{
          display: inline-block;
          width: 60%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow:ellipsis;
        }
        .pub-date{
          display: inline-block;
          vertical-align: top;
          width:35%;
          text-align: right;
        }
        .read-count{
          vertical-align: top;
          display: inline-block;
          text-align:right;
          width: 10%;
        }
        &:hover{
           color:deepskyblue;
        }
      }
    }
     .article-content{
      transform: rotateY(180deg);
      line-height: 22px;
      font-size: 16px;
      color: #999;
       display: flex;
        height: 100%;
        flex-direction: column;
       .article-content-inner{
         flex: 1; 
         text-indent: 20px;
         padding: 10px;
       }
      .back{
        cursor: pointer; 
        width: 100px;
        height: 25px;
        border: 1px solid #999;
        border-radius: 5px;
        font-size: 14px;
        text-align: center;  
        line-height: 25px; 
        margin: 0 auto;
        &:hover{
          background-color: lightblue;
        }
        span{
            display: inline-block; 
        }
      }
    }
  }
}
 
</style>



