import * as types from './mutation-types'
import { rearrange } from '../utils'
const mutations = {
  [types.SET_INVERSE](state, data) {
    state.images.some(img => {
      if (img.id === data.id) {
        img.isInverse = !img.isInverse
        return true
      }
      return false
    })
  },
  [types.SET_CENTER](state, data) {
    state.images.some(img => {
      if (img.id === data.id) {
        img.isCenter = true
        return true
      } else {
        img.isCenter = false
        img.isInverse = false
        return false
      }
    })
  },
  [types.RE_RANGE](state, data) {
    state.images = rearrange(state.images, Math.round(data.id))
  }
}
export default mutations



