<template>
    <nav class="controls">
        <template  v-for="(item,index) in getImages">
          <span :class="['controller-unit',{'is-center':item.isCenter,'is-inverse':item.isInverse}]" @click.stop.prevent="handleClick(item)"  :key="item.id"></span>
        </template>
      </nav>
</template>

<script type="text/ecmascript-6">
import { mapActions, mapGetters } from 'vuex'
export default {
  data() {
    return {}
  },
  methods: {
    ...mapActions(['reRange', 'setInverse', 'setCenter']),
    handleClick(item) {
      const { id } = item
      if (item.isCenter) {
        this.setInverse({ id })
      } else {
        this.setCenter({ id })
        this.reRange({ id })
      }
    }
  },
  computed: {
    ...mapGetters(['getImages'])
  }
}
</script>

<style scoped lang="less">
@import "../../common/less/mixins";

.controls {
  position: absolute;
  z-index: 101;
  bottom: 30px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 20%;
  box-sizing: border-box;
  .controller-unit{
      background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
      .bg-img('./timg1.png');
    height: 20px;
    width: 20px;
    //  .heart(#aaa);
    cursor: pointer;
    // transition: transform 0.6s ease-in-out, background 0s;
    &.is-center{
      // .heart(red);
    
    .bg-img('./timg.png');
        -webkit-animation-name: scaleDraw;
        -webkit-animation-timing-function: ease-in-out;
        -webkit-animation-iteration-count: infinite;
        -webkit-animation-duration: 1.5s;
        -webkit-animation-direction: alternate;
      &.is-inverse{
        
        
      }
    }
  }
}
@keyframes scaleDraw {
      0% {
          -webkit-transform:scale(1);
      }
      25% {
          -webkit-transform:scale(1.2);
      }
      50% {
          -webkit-transform:scale(1);
      }
      75% {
          -webkit-transform:scale(1.2);
      }
  }
</style>



